<template>
  <section class="worker-list-page">
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 10px; text-align: center;">
      <el-button size = "small" type="primary" @click="handleAdd">新增</el-button>
    </el-col>

    <!--列表-->
    <el-table :data="workers" highlight-current-row v-loading="listLoading" @selection-change="selsChange" border
              stripe
              style="width: 100%; font-size: 10px; text-align: center">
      <el-table-column label="会员头像" width="100">
        <template scope="scope">
          <div slot="reference" class="name-wrapper">
            <img style="width: 80px;height: 80px;border-radius: 40px;margin-top: 5px;margin-bottom: 5px"
                 :src="scope.row.portrait"/>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="no" label="编号" width="80" sortable></el-table-column>
      <el-table-column prop="department" label="营业部" width="100"></el-table-column>
      <el-table-column prop="wechatId" label="微信openId" width="100"></el-table-column>
      <el-table-column prop="name" label="姓名" width="80" ></el-table-column>
      <el-table-column prop="male" label="性别" width="90" :formatter="formatMale"></el-table-column>
      <el-table-column prop="phone" label="手机号码" width="120"></el-table-column>
      <el-table-column prop="birthday" label="生日" width="120" :formatter="formatBirthDay" sortable></el-table-column>
      <el-table-column prop="cardNo" label="卡号" width="100" sortable></el-table-column>
      <el-table-column prop="joinDay" label="加入日期" width="120" :formatter="formatJoinDate" sortable></el-table-column>
      <el-table-column prop="workType" label="工种" width="120" :formatter="formatWork" sortable></el-table-column>
      <el-table-column label="操作" width="120" >
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="small" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--新增界面-->
    <el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addWorker" label-width="80px">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="addWorker.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model="addWorker.phone"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio-group v-model="addWorker.male">
            <el-radio class="radio" label="男"></el-radio>
            <el-radio class="radio" label="女"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="微信ID" prop="wechatId">
          <el-input v-model="addWorker.wechatId" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="户籍" prop="houseHold">
          <el-input v-model="addWorker.houseHold" auto-complete="off"></el-input>
        </el-form-item>
        <!--<el-form-item label="营业部" prop="department">-->
          <!--<el-dropdown-menu v-model="addWorker.department">-->
            <!--<el-dropdown-item >总部</el-dropdown-item>-->
          <!--</el-dropdown-menu>-->
        <!--</el-form-item>-->

        <!--<el-form-item label="职责" prop="workType">-->
          <!--<el-radio-group v-model="addWorker.workType">-->
            <!--<el-radio class="radio" :label="false">否</el-radio>-->
            <!--<el-radio class="radio" :label="true">是</el-radio>-->
          <!--</el-radio-group>-->
        <!--</el-form-item>-->

        <!--<el-form-item label="会员头像">-->
          <!--<el-upload-->
            <!--class="avatar-uploader"-->
            <!--:action="uploadPictureUrl"-->
            <!--:show-file-list="false"-->
            <!--:on-success="handleAvatarSuccess"-->
            <!--:before-upload="beforeAvatarUpload">-->
            <!--<img v-if="addWorker.headportraitUrl" :src="addWorker.headportraitUrl" class="avatar">-->
            <!--<i class="el-icon-plus avatar-uploader-icon" v-else></i>-->
          <!--</el-upload>-->
        <!--</el-form-item>-->


      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false">取消</el-button>
        <el-button type="primary" @click.native="addMember" :loading="addMemberLoading">提交</el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
  import ElDropdownMenu from "../../../node_modules/element-ui/packages/dropdown/src/dropdown-menu.vue";
  import ElDropdownItem from "../../../node_modules/element-ui/packages/dropdown/src/dropdown-item.vue";

  export default {
    components: {
      ElDropdownItem,
      ElDropdownMenu},
    data(){
      return {
        addFormVisible: false,
        addWorker:{
          no:0,
          portrait:'',
          name:'',
          department: '',
          birthday:1538139928281,
          joinDay:1538139928281,
          wechatId:'',
          phone:'',
          male:1,
          houseHold:'',
          address: '',
          comments: '',
          workType: 1,
          cardNo: ''}
      }
    },

    computed: {
      workers(){
        return this.$store.getters.getMembers
      },
      listLoading(){
        return false
      },
      uploadPictureUrl(){
        return  'www.baidu.com/api/coffees/uploadPicture'
      },
      addMemberLoading(){
        return this.$store.getters.addMemberLoading;
      },

      editMemberLoading(){
        return this.$store.getters.editMemberLoading;
      },
    },

    mounted(){
      this.$store.dispatch("loadMembers", 0)
    },

    watch: {
      addMemberLoading(loading) {
        this.addFormVisible = loading;
      },
    },

    methods: {
      handleAdd(){
        this.addFormVisible = true;
      },

      selsChange: function (sels) {
        this.sels = sels;
      },

      handleEdit: function (index, row) {
//        this.editFormVisible = true;
//        this.editMemberForm = Object.assign({}, row);
      },
      handleDelete:function (index, row) {
        this.$store.dispatch("removeMember", row)
      },

      handleAvatarSuccess(res, file) {
        this.addWorker.headportraitUrl = res.data
      },

//      handleEditAvatarSuccess(res, file){
//        this.editMemberForm.headportraitUrl = res.data;
//      },

      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isLt2M;
      },

      formatBirthDay: function (row, column){
//        let newDate = new Date();
//        newDate.setTime(row.birthday)
//        return newDate.format('yyyy年MM月dd日 hh:mm')
      },

      formatJoinDate: function (row, column){
//        let newDate = new Date();
//        newDate.setTime(row.joinDay)
//        return newDate.format('yyyy年MM月dd日 hh:mm')
      },
      addMember(){
        this.$store.dispatch('addMember', this.addWorker)
      },

      formatMale: function (row){
        switch (row.male) {
          case 1:
            return "男"
          case 2:
            return "女"
          default:
            return "保密"
        }
      },
      formatWork: function (row) {
        switch (row.workType) {
          case 0:
            return "管理员"
          case 1:
            return "前台"
          case 2:
            return "维修工"
            return
          case 3:
            return "业务员"
          default:
            return "保密"
        }
      }
    }
  }
</script>

<style lang="scss">
  .worker-list-page {
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
      border-color: #20a0ff;
    }

    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
    }

    .avatar {
      width: 178px;
      height: 178px;
      display: block;
    }

  }
</style>



